/*! CSS Used from: https://rule.neemia.fi/css/style.css */
/*! @import https://rule.neemia.fi/css/common.css */
*{margin:0px;padding:0px;font-family:Roboto, Tahoma, Geneva, sans-serif;}
td{padding:0px;margin:0px;}
td{padding:2px 6px 2px 4px;}
#menu_level2{font-size:15px;}
ul.buttons_navigate_lvl_2{margin-top:0px;padding-left:30px;padding-top:8px;}
.buttons_navigate_lvl_2 li{display:inline;list-style:none;}
.buttons_navigate_lvl_2 li a{float:left;margin-top:auto;margin-bottom:auto;margin-right:15px;background-image:none;}
.buttons_navigate_lvl_2 li a span{color:#fff;}
.buttons_navigate_lvl_2 li a:hover span{color:#0070B2;background-image:none;}
.buttons_navigate_lvl_2 li.selected_item{display:inline;list-style:none;}
.buttons_navigate_lvl_2 li.selected_item a span{color:#22ADFF;}
a:link{text-decoration:none;color:#0000EE;}
a:visited{text-decoration:none;color:#0000EE;}
a:hover{color:#0070B2;text-decoration:none;}
a:active{text-decoration:none;color:#22ADFF;}
#menu_level2{margin:0px;text-align:center;margin-right:auto;margin-left:auto;}
#menu_level2{background-color:#505050;height:35px;margin:0px;padding:0px;margin-right:auto;margin-left:auto;}
#div_innercontent{margin:0 auto;}
#div_content{padding:10px;background-color:#FFFFFF;height:100%;}
.page_header{margin:5px 0px;height:28px;background-color:#C4D9FE;border:1px solid #90BCFD;padding:0px;padding-top:5px;}
.page_header h2{margin:0px;color:#404050;vertical-align:bottom;text-decoration:none;text-align:center;}
