/*! CSS Used from: https://rule.neemia.fi/css/style.css */


/*! @import https://rule.neemia.fi/css/common.css */

* {
    margin: 0px;
    padding: 0px;
    font-family: Roboto, Tahoma, Geneva, sans-serif;
}

footer {
    display: flex;
    justify-content: space-around;
    width: 100%;
    margin: 0 auto;
    background-color: #3D3D3D;
    background-repeat: repeat-x;
    color: white;
    position: absolute;
    bottom: 0;
}

footer div {
    display: flex;
    justify-content: space-between;
    width: 80%;
    padding: 10px;
}

footer a:link {
    text-decoration: underline;
    color: white;
}

footer a:visited {
    text-decoration: underline;
    color: white;
}

footer a:hover {
    color: #22ADFF;
    text-decoration: underline;
}

footer a:active {
    text-decoration: underline;
    color: #22ADFF;
}

a:link {
    text-decoration: none;
    color: #0000EE;
}

a:visited {
    text-decoration: none;
    color: #0000EE;
}

a:hover {
    color: #0070B2;
    text-decoration: none;
}

a:active {
    text-decoration: none;
    color: #22ADFF;
}


